@import "../../css/share.scss";
.basic_layout {
    background: #171a30;
    height: 100vh;
    width: 100%;

    &-header{
        height: px(86);
        border-bottom: 1px solid #393939;
        display: flex;

        .title{
            font-size: px(30);
            line-height: px(86);
            color: #ffffff;
            text-align: center;
            flex-grow: 1;
        }

        .leading{
            width: px(100);
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;

            img{
                width: px(40);
                height: px(40);
                display: block;
            }
        }

        .action{
            width: px(100);
            height: 100%;
        }
    }
}
