@import "../../css/share.scss";
.dapp_card {
    padding: px(32) px(34);
    display: flex;
    color: #fff;

    .img {
        flex-grow: 0;
        flex-shrink: 0;
        width: px(147);
        height: px(147);
        border-radius: px(10);
        overflow: hidden;
        margin-right: px(30);

        img {
            width: 100%;
            height: 100%;
        }
    }

    .info {
        flex-grow: 1;

        .t1{
            font-size: px(30);
            line-height: 1.5;
        }

        .t2{
            font-size: px(28);
            color: #FFBE81;
            line-height: 1.5;
        }

        .t3{
            font-size: px(24);
            color: #C1C1C1;
            line-height: 1.5;
        }
    }

    .tag{
        flex-grow: 0;
        flex-shrink: 0;
        width: px(146);
        height: px(110);
        margin-top: px(19);

        img{
            width: 100%;
            height: 100%;
        }
    }
}
